@charset "utf-8";
*{margin:0px;padding:0px;}
ul, li, ol{list-style:none;}
body{
  font-family: "맑은 고딕",arial, sans-serif;
  font-size:12px;
  color:#333;
}
a{text-decoration:none;color:#333;}

header{
  width:100%;height:156px;
  box-shadow:0px 0px 3px #333;
}
.header_inner{
  width:1050px;height:156px;
  margin:0px auto;
  position: relative;
}
.header_inner > ul{
  display:flex;
  padding-left: 870px;
}
.header_inner .sign{
  padding-right: 20px;
}
.header_inner .login{
  padding-right: 20px;
}
.header_inner .sign{
  color:rgb(95, 0, 128);
}
.header_inner img{
  padding-top: 30px;
  display:flex;
}
.header_inner .location{
  position:absolute;
  left: 900px;
  top: 10px;
}
.header_inner .heart{
  position:absolute;
  left: 950px;
  top: 10px;
}
.header_inner .cart{
  position:absolute;
  left: 970px;top: 10px;
}
.header_inner input{
  width:400px; height:48px;
  border-radius:6px;
  border: 1px solid rgb(95, 0, 128);
  margin: -45px 324px;
  position: absolute;
  padding-left:10px;
}
.toggle{position: relative;}
.categori{position: absolute;
  top: 28px;
  left: 30px;
  font-size: 15px;
  font-weight: bolder;
}
.add0 > ul{
  margin: 0 250px;
  display: flex;
  font-size: 15px;
  font-weight: bolder;
}
.add{
  padding-left:70px;
}
.add2{
  padding-left:70px;
}
.add3{
  padding-left:70px;
}
.add4{
  padding-left:70px;
}


.slide-wrapper{
  width:1900px;height:370px;
  margin:0px auto;
  position: relative;
  overflow: hidden;
}
.slide{
  position:absolute;
  display:flex;
  width:19000px;
  left:0px;top:0px;
  transition:0.3s;
}
.slide > li > img{
  width: 100%;
}
.slide .main{
  width:1900px;height:370px;
  float:left;
}
.btn_group > input[type=button]{
  position:absolute;top:47%;
  padding: 5px;
}
.btn_group .next{
  right:0px;
}
.btn_group .prev{
  left:0px;
}

main, .product{
  width:100%;
}
main .m_title{
  display:none;
}
.slide{
  width:100%;
}
.slide img{
  width:100%;
}
.product h3{
  text-align:center;
  font-size:30px;
  margin-top: 100px;
}
.product ul{
  display: flex;
  justify-content: space-around;
  width:1050px;
  margin: 0 auto;
  padding: 40px 0px;
}
.product img{
  width:249px; height:320px;
  transition: 0.5s;
}
.product img:hover{
  width: 252px;height: 323px;
  transition: 0.5s;
}
.product p{
  font-size:15px;
}
.product .price{
  padding-top: 10px;
  font-weight: bolder;
  position:absolute;
}
.product .per{
  font-weight:bolder;
  color: #f00;
}
.banner{
  width:1050px;
  margin: 0 auto;
  display: block;
  padding:50px 0px;
}
.top_btn:hover{
  background: #fff;
    color: rgb(61, 61, 61);
    animation: btn_ari 1s infinite;
    bottom: 30px;
}
.top_btn{
  position: fixed;
    right: 30px;
    bottom: 30px;
    z-index: 100;
    display: inline-block;
    padding: 10px 10px;
    background: rgb(61, 61, 61);
    color: rgb(255, 255, 255);
    border: 2px solid #fff;
    border-radius: 100px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 0px 6px #333;
    transition: 0.4s;
    opacity: 0;
}
.top_btn i {
  font-size: 30px;
}
.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  -webkit-font-feature-settings: 'liga';
  -webkit-font-smoothing: antialiased;
}



footer{
  width:100%;height:580px;
  padding-top:30px;
  border-top:1px solid #ccc;
}
.f_inner{
  width:1050px;
  margin:0px auto;
  height:580px;
}
